.App {
  text-align: center;
}

.Letters {
  margin: auto;
  width: 100%;
  height: 500px;
}

.Letter {
  min-width: 30px;
  min-height: 30px;
  margin: 3px;
  display: inline-block;
  text-align: center;
  border-width: 2px;
  border-color: black;
  border-radius: 10%;
  background: beige;
  color: black;
  font-weight: bold;
  -webkit-box-shadow: 2px 2px #ddddc1;
          box-shadow: 2px 2px #ddddc1;
}

.TilesSection {
  overflow: hidden;
}

.Board {
  overflow: hidden;
  height: 100px;
}

.WordBuilder.invalid > .Letter {
  background: lightgray;
  color: gray;
  -webkit-box-shadow: 2px 2px darkgray;
          box-shadow: 2px 2px darkgray;
}

.WordBuilder > .Letter {
  height: 60px;
  width: 60px;
}

.WordBuilder > .Letter > .LetterText {
  position: relative;
  top: 30%;
  -webkit-transform: translateY(10%);
      -ms-transform: translateY(10%);
          transform: translateY(10%);
}

.WordBuilder {
  height: 80px;
}

.Word {
  margin-top: 15px;
  margin-right: 30px;
  display: inline-block;
}

.LetterText {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(70%);
      -ms-transform: translateY(70%);
          transform: translateY(70%);
}

.YourWords {
  float: left;
  height: 100%;
  width: 50%;
}

.YourWords   .Letter {
  background: lightcyan;
  -webkit-box-shadow: 2px 2px cyan;
          box-shadow: 2px 2px cyan;
}

.OpponentWords {
  float: right;
  width: 50%;
  height: 100%;
}

.OpponentWords   .Letter {
  background: lightgoldenrodyellow;
  -webkit-box-shadow: 2px 2px goldenrod;
          box-shadow: 2px 2px goldenrod;
}

.AllWords {
  width: 100%;
}

.WaitingScreen {
  -ms-flex-item-align: center;
      align-self: center;
  border: 2px dashed black;
}

.GameUrl {
  font-family: monospace
}


